.App {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-Margin-L10 {
  margin-left: 10px;
}

.App-Margin-R10 {
  margin-right: 10px;
}

.App-Header {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #764abc;
}

.App-Header h1 {
  font-size: 25px;
  font-weight: 400;
  color: #ffffff;
  margin-left: 15px;
}

.App-Main {
  flex: 1;
  width: 88%;
  max-width: 1280px;
  background-color: #ffffff;
  margin: 30px auto;
  padding: 20px;
  border: 1px solid #e5e1eb;
}

.App-Footer {
  height: 40px;
  text-align: center;
  background-color: #764abc;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 300;
}

.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:focus-visible {
  background-color: #764abc !important;
  border-color: #764abc !important;
}
